<template>

  <form class="d-flex flex-column" action="login" method="post">
    <span class="answer text-center" i-d-b-database="answerForRecovery1"></span>
    <input class="form-control" type="text" placeholder="" v-model="state.keyInput" />
    <div class="px-5 my-4">
      <button type="button" class="btn btn-outline-primary w-100" @click="certify()"></button>
    </div>
  </form>
</template>
<script>
import store from '@/store';
import { reactive } from '@vue/reactivity'
export default {
  name: 'PhoneCheck',
  props: ["keys","phone"],
  setup(props) {
 
    const state = reactive({
      keys: props.keys,
      keyInput: ""
    })

    const certify = async () => {

      if (state.keysInput === '') {
        alert(' .');
        return false;
      } else if (state.keysInput !== state.keys) {
        alert('  .')
        return false;
      } else {
        store.commit("setPhone", props.phone )
        alert('.')
      }
    }
    return { certify, state }
  }
}

</script>