<template>
  <div class="input-form2 col-md-12 mx-auto">
    <h4 class="mb-3"> </h4>
    <form class="validation-form" @submit.prevent>
      <div class="row">
        <div class="col-md-6 mb-3">
          <label for="name"></label>
          <input type="text" required autofocus class="form-control" v-model="state.name" id="name" placeholder="">
        </div>
        <div class="col-md-6 mb-3">
          <label for="nickname"></label>
          <input type="text" required autofocus class="form-control" v-model="state.nickname" id="nickname"
            placeholder="" @keyup="nickCheckHandler">
          {{ state.nickCheck }}
        </div>
      </div>
      <div class="mb-3">
        <label for="email"></label>
        <input type="email" class="form-control" v-model="state.email" id="email" placeholder="">
      </div>
      <button @click="emailVali()" class="btn btn-outline-primary w-100" v-if="(state.ch2 == 0)">
          
      </button>
      <button @click="emailCheck()" class="btn btn-outline-primary w-100 mt-3" v-if="(state.ch == 1)">
         
      </button>
      <EmailCheck v-if="(state.change == 1)" :keys="state.keys" :email="state.email" />
      <div class="mb-3">
        <label for="password"></label>
        <input type="password" class="form-control" v-model="state.password" id="password" placeholder="">

      </div>
      <div class="mb-3">
        <label for="repassword"></label>
        <input type="password" class="form-control" v-model="state.repassword" id="repassword" placeholder=" ">
      </div>
      <div class="mb-3">
        <label for="phone"></label>
        <input type="text" class="form-control" v-model="state.phone" id="phone" placeholder=" ) 01080094125"
          @blur="phoneCheck">
        {{ state.phoneCheck }}
      </div>
      <div class="mb-3">
        <label for="city"></label>
        <input type="text" class="form-control" v-model="state.city" id="city" placeholder=" )  ">
      </div>
      <div class="mb-3">
        <label for="street"></label>
        <input type="text" class="form-control" v-model="state.street" id="street" placeholder=" ) 123">
      </div>
      <div class="mb-3">
        <label for="zipcode"></label>
        <input type="text" class="form-control" v-model="state.zipcode" id="zipcode" placeholder=" ) 12345">
      </div>

      <hr class="mb-4">
      <div class="custom-control custom-checkbox">
        <input type="checkbox" class="custom-control-input" v-model="state.aggrement" id="aggrement">
        <label class="custom-control-label" for="aggrement">    .</label>
      </div>
      <div class="mb-4"></div>
      <button class="btn btn-primary btn-lg btn-block" type="submit" @click="joinHandler"> </button>
    </form>
  </div>
</template>

<script>
import { reactive } from '@vue/reactivity'
import axios from 'axios'
import router from '@/router'
import store from '@/store'
import EmailCheck from './EmailCheck.vue'
export default {
  name: 'ToJoin',
  setup() {

    const state = reactive({
      form: {
        id: "1",
        email: "",
        password: "",
        role: "2",
      },
      id: '',
      email: '',
      password: '',
      repassword: '',
      name: '',
      nickname: '',
      phone: '',
      city: '',
      street: '',
      zipcode: '',
      change: 0,
      keys: '',
      emailVal: '',
      ch: 0,
      ch2: 0
    })

    const emailCheck = () => {

      state.change = 1;
      if (state.email === "") {
        alert(" ");
        state.email.value.focus();
        return false;
      }
      const url = "/api/passFind";
      const headers = {
        "Content-Type": "application/json",
      };
      console.log("---------------------");
      console.log(state.email);
      const body = { email: state.email };
      axios.get(url, body, { headers }).then(function (res) {
        console.log(res);
        if (res.data.email === state.email) {
          alert("  .")
          state.change = 0;
        } else if (res.data != "" || res.data.email != state.email) {
          state.ch2 = 1;
          console.log(typeof (res.data));
          alert("   .");
          document.getElementById('email').readOnly = true;
          axios.post("/api/join/emailCheck", body, { headers }).then(function (res) {
            console.log("---------------------------");
            console.log(res);
            console.log(res.data.keys);
            state.keys = res.data.keys;
            state.emailVal = res.data.email;
          });
          state.ch = 0;
        } else {
          alert("  ");
        }
      });
    }

    const asd = async () => {
      alert("asd");
      const url = "./member/login"
      const headers = { "Content-Type": "application/json; charset=utf-8;" }
      const body = { id: state.form.id, email: state.form.email, password: state.form.password, role: state.form.role };
      await axios.post(url, body, { headers }).then(function (res) {
        store.commit("setToken", res.data.token);
        store.commit("setId", state.form.id);
        store.commit("setEmail", res.data.email);
        store.commit("setRole", "2")
        console.log(res.data);
        alert(".");
      })
    }
    const nickCheckHandler = async () => {
      const url = '/api/nickVali'
      const headers = { "Content-Type": "application/json" }
      const body = { nickname: state.nickname }
      const response = await axios.post(url, body, { headers })
      if (response.status === 200) {
        state.nickCheck = (response.data.result === 1) ? "" : ""
      } else {
        state.nickCheck = " "
      }
    }

    const emailVali = async () => {
      const url = "/api/emailVali"
      const headers = { "Content-Type": "application/json; charset=utf-8;" }
      console.log(state.email)
      console.log("===============")
      const body = { email: state.email };
      console.log(state.email);
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.data.validate === true) {
          alert("  .")
          return false;
        } else if (state.email === '') {
          alert(' ');
          state.email.value.focus();
          return false;
        } else if (!(state.email.includes("@") && state.email.includes("."))) {
          alert('   .');
          state.email.value.focus(); return false;
        } else (res.data.validate === false)
        alert("  .")
        state.ch = 1
      })
    }

    const phoneCheck = async () => {

      const url = '/api/phoneVali'
      const headers = { "Content-Type": "application/json" }
      const body = { phone: state.phone }
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.data.validate === true) {
          state.phoneCheck = ""
        } else if (res.data.validate === false && state.phone.length >= 10) {
          state.phoneCheck = ""
        }
      })
    }

    const joinHandler = async () => {
      const url = './api/memberRegister'
      const headers = {
        "Content-Type": "application/json",
      }
      const body = {
        id: state.id,
        email: state.email,
        password: state.password,
        name: state.name,
        phone: state.phone,
        city: state.city,
        street: state.street,
        zipcode: state.zipcode,
        nickname: state.nickname,
      }

      var n_RegExp = /^[-]{2,15}$/;

      if (state.name === '') {
        alert(' ');
        state.name.value.focus(); return false;
      } else if (state.name.length >= 8) {
        alert(' 8  ')
        state.name = null;
        state.name.value.focus(); return false;
      } else if (!n_RegExp.test(state.name)) {
        alert(",,. .");
        state.name = null;
        state.name.value.focus(); return false;
      } else if (state.nickname === '') {
        alert(' ');
        state.nickname.value.focus(); return false;
      } else if (state.nickname.length >= 8) {
        alert(' 8  ')
        state.nickname = null;
        state.nickname.value.focus(); return false;
      } else if (state.email === '') {
        alert(' ');
        state.email.value.focus(); return false;
      } else if (!(state.email.includes("@") && state.email.includes("."))) {
        alert('   .');
        state.email = null;
        state.email.value.focus(); return false;
      } else if (state.password === '') {
        alert(' ');
        state.password.value.focus(); return false;
      } else if (state.repassword === '') {
        alert(' ');
        state.repassword.value.focus(); return false;
      } else if (state.phone === '') {
        alert(' ');
        state.phone.value.focus(); return false;
      } else if (state.phone.length <= 10) {
        alert(' 10  ')
        state.phone = null;
        state.phone.value.focus(); return false;
      } else if (state.city === '') {
        alert(' ');
        state.city.value.focus(); return false;
      } else if (state.street === '') {
        alert(' ');
        state.street.value.focus(); return false;
      } else if (state.zipcode === '') {
        alert(' ');
        state.zipcode.value.focus(); return false;
      } else if (state.password != state.repassword) {
        alert('  .  ');
        state.password.value.value = '';
        state.repassword.value.value = '';
        state.password.value.focus(); return false;
      }

      console.log(store.state.email);
      if (store.state.email === 0) {
        return false;
      } else {
        const response = await axios.post(url, body, { headers })
        // console.log(response.data)
        if (response.status === 200) {
          alert(' .');
        } else {
          alert(' .')
        }
        router.push({ name: "Login" });
      }
    }
    return { joinHandler, state, emailCheck, emailVali, asd, nickCheckHandler, phoneCheck }
  },
  components: { EmailCheck }
};
</script>

<style scoped>
.container2 {
  height: 100vh;
}

.input-form2 {
  text-align: center;
  padding: 32px;

  /* margin-top: 45px; */

  background: #fff;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
}

#hero {
  overflow: scroll;

  max-height: 1100px;
}

#hero::before {
  height: 1100px;
}
</style>