<template>
  <MyModal  ref="baseModal">
    <div class="content-container">
      <form class="validation-form" @submit.prevent>
        <h3 class="answer text-center" id="answerForRecovery1"> </h3>
        <div class="mb-3">
          <label for="password"></label>
          <input type="password" class="form-control" v-model="state.password" id="password" required>
          <div class="invalid-feedback">
             .
          </div>
        </div>
      </form>
    </div>
    <div class="buttons-container">


      <button class="btn btn-primary mr-2" @click="check"></button>
      <button class="btn btn-warning" @click="cancel"></button>


    </div>
  </MyModal>
</template>
  
<script>
import MyModal from "./MyModal.vue";
import { reactive, ref } from "vue";
import axios from "axios";
import store from "@/store";
import router from "@/router";

  
export default {
  name: "ConfirmationModal",
  components: {
    MyModal,
  },
  //   .
  props: {
  },
  setup() {

    const state = reactive({
      id : store.state.id,
      password : "",
    })
    //     ref
    const baseModal = ref(null);
    // Promise    ref
    const resolvePromise = ref(null);

    const show = () => {
      // baseModal  .
      baseModal.value.open();
      // Promise  ,    / 
      //        .
      return new Promise((resolve) => {
        // resolve    .
        resolvePromise.value = resolve;
      });
    };

    const confirm = () => {
      baseModal.value.close();
      resolvePromise.value(true);
    };

    const cancel = () => {
      baseModal.value.close();
      resolvePromise.value(false);
    };
    
    const check = async() => {
      if (state.password === '') {
      alert(' ');
      state.password.value.focus();
      return false;
      } 
      const url = '/api/passCheck'
      const headers = {
        "Content-Type": "application/json",
      }
      const body = { 
        id : state.id,
        password: state.password
      };
      console.log("==========================================");
      console.log(state.password);
      axios.post(url, body, { headers }).then(function (res) {
        console.log(res);
        if (res.data === false) {
          alert("  .")
          return false;
        } else (res.data === true)
          alert(" .")
          router.push({ name: "PassChange2" })
          });
        }
    return { baseModal, show, confirm, cancel, state, check };
  }
};
</script>

<style scoped>
.buttons-container{
  text-align: center;
}
.btn-primary{
  margin-right: 10px;
}
.validation-form{
  text-align: center;
}
.modal-container{
  border-radius: 10px;
}
</style>