<template>
    <!-- teleport:    . -->
    <teleport to="body">
      <!-- Modal   . -->
      <div class="backdrop" v-if="state.isVisible">
        <div class="modal-container">
          <!-- slot  BaseComponent . -->
          <slot></slot>
        </div>
      </div>
    </teleport>
  </template>
  
  <script>
//   import { ref } from "vue";
import {reactive} from '@vue/reactivity'
  export default {
    name: 'ToBeModal',
    setup() {
        const state = reactive({
            isVisible : 0
        })
        //      .
        const open = async() => {
            state.isVisible = 1;
        };
    
        const close = async() => {
            state.isVisible = 0;
        };
        
        // setup  ,    .
        return {
            open,
            close,
            state
        };
    },
  };
  </script>
  
  <style lang="scss" scoped>
  .backdrop {
    z-index: 999;
    display: flex;
    position: fixed;
    align-items: center;
    justify-content: center;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba($color: #000000, $alpha: 0.2);
  
    .modal-container {
      background: white;
      max-width: 320px;
      width: 100%;
      padding: 1.5rem;
      border-radius: 10px;
    }
  }
  </style>